<template lang="pug">
  .frame
    .toplist
      router-link(to="/",class="list") 首頁
      router-link(to="/record",class="list") 簡歷 
      router-link(to="/design",class="list") 繪圖設計
      router-link(to="/entity",class="list") 實體作品
    .bottomcontent
      transition(name="page",mode="out-in")
        router-view(:key="$router.path")
</template>

<script>
export default {
  name: 'App'
}
</script>

<style lang="sass">
@import url(https://fonts.googleapis.com/earlyaccess/cwtexyen.css)

*
  // border: 1px solid white
  font-family: "cwTeXYen", sans-serif

@mixin size($w,$h:$w)
  width: $w
  height: $h

.page-enter-active, .page-leave-active
  transition: 0.5s
.page-enter, .page-leave-to
  opacity: 0

html,body
  position: relative
  z-index: -1
  display: flex
  justify-content: center
  align-items: center
  margin: 0
  padding: 0
  +size(100%)
  background-color: #222

.frame
  +size(auto,100%)
  display: flex
  flex-direction: column

.bottomcontent
  position: relative
  z-index: 2
  display: flex
  // margin-top: 30px
  +size(100%,90%)

a
  text-decoration: none
  
.toplist
  position: relative
  z-index: 5
  height: 50px
  display: flex
  justify-content: center
  align-items: center
  margin: 15px
  .list
    color: rgba(white,0.8)
    +size(160px,40px)
    margin: 5px
    font-size: 24px
    display: flex
    justify-content: center
    align-items: center
    cursor: pointer
    &:hover
      border-bottom: 2px solid rgba(white,0.8)
    &:active
      border-bottom: none

@media (max-width: 600px) 
  .toplist
    position: fixed
    top: 0
    left: 50%
    transform: translateX(-50%)
    margin: 0
    display: flex
    width: 100%
    height: 50px
    z-index: 50
    background-color: #222
    border-bottom: 1px solid rgba(white,0.5)
    .list
      padding: 0px 10px
      font-size: 24px
      +size(95px,30px)
      &:hover
        border-bottom: none
      &:active
        color: yellow

</style>



